import { hotjar } from "react-hotjar"
import { HotjarConfig, UserDataHotjar } from "./types"

const initHotjar = (config: HotjarConfig) => {
  if (config.enabled && config.hjId) {
    hotjar.initialize(config.hjId, 0)
  }
}
const identifyHotjar = (user: UserDataHotjar) => {
  const isHotjarInitialized = hotjar.initialized()
  if (!isHotjarInitialized) return

  hotjar.identify(user.userEmail, {
    email: user.userEmail,
    firstName: user.firstName,
    lastName: user.lastName,
    company: user.companyName,
  })
}

export { initHotjar, identifyHotjar }
